*{
    margin: 0;
    
}


.container{
    width:100%;
    margin: 0 auto;
    
}

.header{
    display: flex;
    background: none;
    background-color: transparent;
    position: fixed;
    width: 100vw;
    margin: 5px;
    margin-top: 5px;
    top: 0;
    z-index: 100;
    justify-content: space-around;
}

.header_logo{
    width: 20%;
    height: 20%;
}

.header_center{

    display: flex;
    justify-content: center;
    align-items: center;
}

.header_center>p{
    margin: 10px;
    text-transform: uppercase;
    padding: 5px;
    font-family:Georgia, 'Times New Roman', Times, serif;
    font-weight: 600;
    
    

}

.header_center>p:hover{
    border-bottom: 3px solid;
    border-radius: 5px;
    

}

.header_right{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-transform: uppercase;
}

.header_right>p{
    margin: 15px;
    border: 3px solid black;
    color: white;
    padding: 20px;
    padding: 10px;
    border-radius: 15px;
    background: black;
}

