.showcase{
    height: 50vh;
    margin-top: 200px;
    width: 80vw;
}



.showcase h1{
    display: flex;
    justify-content: center;
    align-content: center;
    margin-bottom: 40px;
}



.containe{
    max-width: 900px;
    padding: 5px;
    margin: 0 auto;
}

.grid1{
    display: grid;
    grid-template-columns: 300px 300px;
    gap: 30px;
    justify-content: center;
    align-items: center;
    height: 100%;

}


.cloth{
    width: 100%;
    height: 100%;
    

}

.cloth img{
    width: 100%;
    height: 100%;
    border-radius: 30px;
    margin-left: 30%;
  
}